.overlay {
    position: relative; 
  }
  
  .overlay::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(1, 17, 38, 0.80);
    z-index: 1;
  }

.box-img{

    height: 100vh;
    width: 100%;

}
.box-img > img{
    height:100vh;
    width: 100%;
    object-fit: cover;
    object-position: 25% 75%;



}
.nav-testo{
    align-items: center;
}
.nav-testo > h1{
    font-weight: bold;
    padding: 10px;
    font-size: 20px;

}

.titolo {
    font-weight: bold;
    font-size: 44px;
    letter-spacing: 4.4px;
    color:white;
}
.titolo span{
    color: #2CB1BF;
}